import { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { createCar, fetchCars } from '../../../../store/Cars/thunks';
import { Dispatcher } from '../../../../types/store';
import { Car, CarResponse, CarsState } from '../../../../types/Cars';
import { CarForm } from '../../../../types/Edit';

export const useSaveCar = (form: CarForm | null, carId: string): [boolean] => {
  const dispatch = useDispatch<Dispatcher>();
  const { postCar, postLoading, postError } = useSelector<{
    cars: CarsState;
  }, CarsState>((state) => state.cars);
  const [body, setBody] = useState<object | null>(null);

  useEffect(() => {
    if (form) {
      const formData = new FormData();
      formData.append('name', form.name);
      formData.append('description', form.description);
      formData.append('priceMin', form.priceMin);
      formData.append('priceMax', form.priceMax);
      formData.append('number', form.number);
      formData.append('thumbnail', form.thumbnail[0]);
      form.colors.forEach((color) => {
        formData.append('colors', color);
      });
      formData.append('categoryId', form.categoryId);
      setBody(formData);
    }
  }, [form]);

  useEffect(() => {
    if (body !== null) {
      if (carId) {
        console.log('hm');
      } else {
        dispatch(createCar(body));
      }
    }
  }, [body]);

  return [postLoading];
};

export const useCarById = (carId: string): [Car, boolean, number] => {
  const dispatch = useDispatch<Dispatcher>();

  const { cars, loading, error } = useSelector<{
    cars: CarsState;
  }, CarsState>((state) => state.cars);

  useEffect(() => {
    dispatch(fetchCars({}, carId));
  }, []);

  return [cars, loading, error];
};
